:root {
  --color-base-white: #ffffff;
  --color-base-blue: #3366CC; 
  --color-base-txt: #4B4B4B;
}

/*Estilos Componente Header */

.header {
  max-width: 100%;
  max-height: 148px;
  background: var(--color-base-white);
  opacity: 1;
}

.header-puntogov{
  width: 100%;
  height: 30px;
  background: var(--unnamed-color-3366cc) 0% 0% no-repeat padding-box;
  background: #3366CC 0% 0% no-repeat padding-box;
}

.header-logogov {
  margin: 2px 0px 0px 80px;
  width: 135px;
  height: 27px;
}

.header-logocrc {
  margin: 8px 0px 0px 80px;
  width: 175px;
  height: 75px;
  background: transparent 0% 0% no-repeat padding-box;
  opacity: 1;
}

/* Estilos componente Section */

section {
  background-color: var(--color-base-white);
  font-size: 14px;
  line-height: 130%;
  color: var(--color-base-txt);
}

section h4 {
  text-align: left;
  font-size: 15px;
  font-weight: bold;
  line-height: 250%;
  color: var(--color-base-txt);
  letter-spacing: 0px;
}

article .list-info {
  padding: 2px 2px 2px 20px;
}

aside {
  padding: 55px 130px 0px 20px;
  font-size: 12px;
}

/* Estilos componente Footer */

footer {
  color: var(--color-base-white);
  font-size: 14px;
  background-color: var(--color-base-blue);
  padding: 35px;
  line-height: 20px;
}

footer a:link, a:visited, a:active {
  text-decoration:none;
  color: var(--color-base-white);
}

footer a:link:hover, a:visited:hover, a:active:hover {
  text-decoration: underline;
}

footer h3 {
  font-size: 16px;
  font-weight: bold;
}

.footer-logos {
  text-align: center;
  width: 100%;
  height: 100%;
}

footer .logo-gov {
  text-align: center;
  margin: 10px 25px 10px 25px;
  width: 100px;
  height: 50px;
}

footer .logo-co {
  text-align: center;
  margin: 10px 25px 10px 25px;
  width: 80px;
  height: 80px;
}


.col-md-2 {
  padding: auto;
  
}

.col-md-5 {
  padding: 40px 40px 40px 40px;
  border-left: 1px solid var(--color-base-white);
  margin: 0px 0px 0px 0px;
}
